// material-ui
import { styled } from '@mui/material/styles';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, tableCellClasses, Button } from '@mui/material';

import AddIcon from '@mui/icons-material/Add';
import Chip from 'ui-component/extended/Chip';
// project imports
import MainCard from 'ui-component/cards/MainCard';
import SecondaryAction from 'ui-component/cards/CardSecondaryAction';

// styles
const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14
    }
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
    },
    // hide last border
    '&:last-of-type td, &:last-of-type th': {
        border: 0
    }
}));

// table data
function createData(title, status, color, date) {
    return { title, status, color,  date };
}

const rows = [
    createData('1. Project Progress of SawingOutsideReport data List', 'pending', "secondary", "1 Nov, 10:20 AM"),
    createData('2 .Project Progress of SawingOutsideReport data List', 'success', "success", "1 Nov, 10:20 AM"),
    createData('3. Project Progress of SawingOutsideReport data List', 'error', "error", "1 Nov, 10:20 AM"),
    createData('4. Project Progress of SawingOutsideReport data List', 'pending', "secondary", "1 Nov, 10:20 AM"),
    createData('5. Project Progress of SawingOutsideReport data List', 'pending', "secondary", "1 Nov, 10:20 AM"),
    createData('6 .Project Progress of SawingOutsideReport data List', 'success', "success", "1 Nov, 10:20 AM"),
    createData('6 .Project Progress of SawingOutsideReport data List', 'success', "success", "1 Nov, 10:20 AM"),
    createData('7. Project Progress of SawingOutsideReport data List', 'pending', "secondary", "1 Nov, 10:20 AM"),
    createData('8 .Project Progress of SawingOutsideReport data List', 'success', "success", "1 Nov, 10:20 AM"),
    createData('9. Project Progress of SawingOutsideReport data List', 'error', "error", "1 Nov, 10:20 AM"),
    createData('10. Project Progress of SawingOutsideReport data List', 'pending', "secondary", "1 Nov, 10:20 AM")
];

// ==============================|| TABLE - CUSTOMIZED ||============================== //

export default function InterimInvoiceList() {
    return (
        <MainCard
            content={false}
            title="SawingOutsideReport Data List"
            secondary={
                <Button variant="contained" startIcon={<AddIcon />}>Create</Button>
            }
        >
            <TableContainer>
                <Table sx={{ minWidth: 320 }} aria-label="customized table">
                    <TableBody>
                        {rows.map((row) => (
                            <StyledTableRow hover key={row.title}>
                                <StyledTableCell sx={{ pl: 3 }} component="th" scope="row">
                                    {row.title}
                                </StyledTableCell>
                                <StyledTableCell align="right"><Chip label={row.status} chipcolor={row.color} /></StyledTableCell>
                                <StyledTableCell align="right" sx={{ pr: 3}}>{row.date}</StyledTableCell>
                            </StyledTableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </MainCard>
    );
}
